<template>
  <transition name="fade">
    <div class="song">
      <header class="song-header">
        <i
          class="iconfont icon-back"
          @click="$router.go(-1)"
        />
        <p class="title">
          歌单
        </p>
      </header>
      <div class="m-list">
        <ul class="recommend-list">
          <li
            class="recommend-list-item"
            v-for="item in recommendSongList"
            :key="item.dissid"
          >
            <a
              class="bg-img"
              :style="{'background-image':`url(${item.imgurl}`}"
              @click="toSongList(`${item.dissid}`,`${item.imgurl}`)"
            >
              <i class="iconfont icon-headset headset">{{ formatNum(`${item.listennum}`) }}</i>
              <i class="iconfont icon-start more" />
            </a>
            <span>{{ item.dissname }}</span>
          </li>
        </ul>
      </div>
    </div>
  </transition>
</template>

<script>
import {hotSongListAPI} from '../api'
import { Indicator } from 'mint-ui'
import util from '../util'
export default {
  name: 'Song',
  data() {
      return {
          recommendSongList:''
      }
  },
  methods: {
      formatNum(num){
       return util.formatNum(num)
     },
       toSongList(id,imgUrl){
         this.$router.push({name:'songList',params:{id},query:{imgUrl}})
       },
        getRecommendSong(){
        Indicator.open('加载中...');
        let page = Math.floor((Math.random()*100)+1); //随机获取第1-10页
        let length = 29; //获取6条
        // console.log(`${hotSongListAPI}?page=${page}&pageSize=${length}`)
        this.axios.get(`${hotSongListAPI}?page=${page}&pageSize=${length}`)
            .then(res=>{
            this.recommendSongList = res.data.data.list
            Indicator.close();

        }).catch(err=>{
            Toast({
            message: '网络异常',
            duration: 5000
            });
            Indicator.close();
        })
     },

  },
  mounted() {
      this.getRecommendSong();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
 .song{
    position: absolute;
    top:0;
    bottom: 0;
    width: 100%;
    z-index: 10;
    background: #f4f4f4;
     .song-header{
        height: 80px;
        align-items: center;
        position: fixed;
        width: 100%;
        z-index:11;
        line-height: 80px;
        background-repeat: no-repeat;
        background-size: 2000%;
        text-align: center;
        background: #fff;
        .title{
          color: #000;
          font-size: $primary-font-size;
        }
        .iconfont{
          color: #000;
          display: block;
          padding: 0 30px;
          font-size: 30px;
          position: absolute;
        }
      }
  }
  .m-list{
      position: absolute;
      top: 80px;
      bottom: 110px;
      overflow: auto;
      margin: 20px 0;
      .recommend-list{
    display: flex;
    flex-wrap: wrap;
    li:nth-of-type(3n){
     margin-right: 0 !important;
    }
    .recommend-list-item{
      flex: 0 0 236px;
      margin-right: 6px;
      margin-bottom: 30px;
    }
    a{
      display: block;
      width: 236px;
      height: 236px;
      background: gray;
      background-size: 100%;
      background-repeat: no-repeat;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      cursor: pointer;
      .more{
        font-size:35px;
        display: block;
        padding: 10px;
      }
      .headset{
        display: block;
        padding: 10px;
        font-size:25px;
      }
    }
    span{
      font-size: $small-font-size;
      display: block;
      text-align: left;
      padding-left: 0.2em;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      word-break: break-all;
      width: 236px;
      box-sizing: border-box;
      height: 40px;
      line-height: 40px;
    }
  }
  }
   
</style>
